.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  font-size: 1rem !important;
  line-height: 29px !important;
  color: #999 !important;
}
.card {
  padding: 3px !important;
  margin: 5px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  color: #202020;
  margin: 0 0 15px;
}
.portfolio-project-data li .project-folio-span {
  display: inline-block;
  width: 130px;
  letter-spacing: 1px;
  color: #202020;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 400;
  margin-left: 0px;
}
.portfolio-section {
  position: relative;
  padding: 20px;
}
.portfolio-main {
}
.project-images {
  padding: 20px;
  background: linear-gradient(to bottom, #1d98e5, #575693);
}
.portfolio-project-data li .project-folio-info {
  font-size: 13px;
}
ul {
  list-style-type: none;
  padding: 0px;
}
p {
  margin: 0 0 30px;
  line-height: 30px;
  font-size: 0.9rem;
}
.mainSection {
  position: relative;
  color: #fff;
  /* background: linear-gradient(to bottom, #7a19f1, #332179); */
  background: linear-gradient(to bottom, #1d98e5, #0d73b3);
  background-attachment: fixed;
  width: 100%;
  top: 30px;
}
.mainIntro {
  text-align: center;
  padding-top: 100px;
  padding-bottom: 100px;
  padding-left: 4px;
  padding-right: 4px;
  max-width: 800px;
  margin: auto;
  font-size: 1rem;
  font-weight: 400;
  color:#202020;
}
.mainIntro span {
  display: block;
  margin-bottom: 4px;
}
.header--fixed {
  position: fixed;
}
.header .bg-light {
  background-color: #fff !important;
}
.footer {
  background: linear-gradient(to bottom, #1d98e5, #0d73b3);
  color: #fff !important;
}
.page-title {
  position: relative;
  background-color: #f5f5f5;
  padding: 25px 0;
  margin-top: 40px;
  text-align: center;
}
.header nav a {
  color: black;
}
.header {
  z-index: 100;
  transition: 0.25s padding-top ease-in-out, 0.25s padding-bottom ease-in-out;
  background: 0 0;
  width: 100%;
}

.contact-section {
  padding: 40px;
}
.subheading {
  margin-top: 60px;
  margin-bottom: 10px;
  z-index: 11111;
  color: #202020;
  text-align: center;
  font-size: 1.5rem;
}
.subheading span {
  display: block;
  color: #999;
  font-size: 1rem;
  margin-top: 8px;
}
.subheading-contact {
  margin-bottom: 10px;
  z-index: 11111;
  color: #fff;
  text-align: center;
  font-weight: bold;
  font-size: 1.5rem;
}
.contact-info span {
  display: block;
}
.contact-info {
  padding: 0px;
  text-align: center;
  font-weight: 400;
}

.bg-dark {
  background-color: #000000 !important;
}
.nav-item {
}
.myContainer {
  background: #e5e9ec;
  min-height: 100vh;
}
.row {
  margin-right: 0px !important;
  margin-left: 0px !important;
}
.card {
  min-height: 120px;
  box-shadow: 0 5px 20px rgb(46 53 64 / 20%), 0 1px 5px rgb(46 53 64 / 30%);
}
.portfolio-btn {
  background: linear-gradient(180deg, #ffdb00 0, #ffcd42 100%) !important;
  box-shadow: 0 2px 3px rgb(0 0 0 / 50%) !important;
  border: none !important;
  color: rgb(0 0 0 / 60%) !important;
  padding: 7px 25px !important;
}
.card-title {
  text-align: center;
  font-size: 1rem;
  font-weight: 600;
}
.card-text {
  font-size: 0.9rem;
  font-weight: 400;
  text-align: center;
}

.myBtn {
  background: #0d73b3 !important;
  background: linear-gradient(180deg, #1d98e5 0, #0d73b3 100%) !important;
  box-shadow: 0 2px 3px rgb(0 0 0 / 50%) !important;
  border: none !important;
  color: #fff;
  padding: 12px 30px !important;
  border-radius: 5px !important;
}
.portfolio-main {
  padding: 10px;
  border-bottom: thin solid lightgrey;
  padding-top: 30px;
  padding-bottom: 30px;
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}

body::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}

body::-webkit-scrollbar-thumb {
  background-color: #000000;
}
.service-box {
  text-align: center !important;
  border-bottom: 1px solid #dee2e6 !important;
  border-right: 1px solid #dee2e6 !important;
  position: relative;
  overflow: hidden;
  min-height: 300px;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  transition: all 0.3s;
}
.project-box
{
  padding:10px!important;
  margin-top:10px;
}
.project-box .service-title
{
  position: absolute;
  bottom:2px;
  padding:10px;
}
.project-box img
{
  width:80%!important;
}
.service-box
{
  padding:20px;
}

.service-box img {
  width: 60%;
  height: auto;
}
.service-box .service-title {
  color: #202020;
  text-align: center;
  font-size: 1.2rem;
}
.service-title span
{
  font-size: 0.8rem;
  display: block;
  text-align: center;
  line-height: 1.5;
  margin-top: 5px;
}
@media screen and (max-width: 600px) {
  .service-box img {
    width: 60%;
    height: 60%;
  }
}
@media screen and (max-width: 600px) {
  .project-box img {
    width: 70%!important;
    height: 70%!important;
  }
}
